/* App.css */

body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background: url('dash-bg.svg');
  background-repeat: no-repeat;
  background-size: cover;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

h1 {
  text-align: center;
}

.plot-container {
  margin-top: 20px;
  text-align: center;
}

button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #0056b3;
}

.loading {
  text-align: center;
  font-style: italic;
  color: #888;
}

.plot {
  background: url('plot-bg.svg');
  background-repeat: no-repeat;
  background-position: center;
}

img {
  width: 150px;
  cursor: pointer;
  box-shadow: #fff;
}